/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {useState, useEffect} from 'react'
import {useSelector, shallowEqual} from 'react-redux'
import {KTSVG, toAbsoluteUrl} from '../../../_metronic/helpers'
import {Link, Navigate} from 'react-router-dom'
import {Dropdown1} from '../../../_metronic/partials'
import {useLocation} from 'react-router'
import {RootState} from '../../../setup'
import { FormattedMessage } from 'react-intl'

const AccountHeader: React.FC = () => {
  const location = useLocation()

  const params = new URLSearchParams(window.location.search);
  const url: string | null = params.get("tab");

  const [tabUrl, setTabUrl] = useState(url)

  useEffect(() => {
    setTabUrl(url)
  }, [url])

  const { userImage } : any = useSelector<RootState>(
    ({auth}) => ({
      userImage: auth.user?.userImage
    })
  )

  return (
    <div className='card mb-5 mb-xl-10'>
      <div className='card-body pb-0'>
        
        <div className='d-flex mb-2' style={{ overflowX: 'auto' }}>
          <ul className='nav nav-stretch nav-line-tabs nav-line-tabs-2x border-transparent fs-5 fw-bolder flex-nowrap'>
            <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'settings' && 'active')
                }
                to='/account?tab=settings'
              >
                <FormattedMessage id='HEADER_USER_MUENU.SETTINGS' />
              </Link>
            </li>
            {/* <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'language' && 'active')
                }
                to='/account?tab=language'
              >
                Language
              </Link>
            </li> */}
            <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'your-wallet' && 'active')
                }
                to='/account?tab=your-wallet'
              >
                <FormattedMessage id='SETTINGS_PAGE.WALLETS' />
              </Link>
            </li>
            {/* <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'transaction-history' && 'active')
                }
                to='/account?tab=transaction-history'
              >
                <FormattedMessage id='SETTINGS_PAGE.TRANSACTIONS' />
              </Link>
            </li> */}
            <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'card-history' && 'active')
                }
                to='/account?tab=card-history'
              >
                Orders
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'royalty-claim-transaction-history' && 'active')
                }
                to='/account?tab=royalty-claim-transaction-history'
              >
                <FormattedMessage id='SETTINGS_PAGE.ROYALTY' />
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'transfer-history' && 'active')
                }
                to='/account?tab=transfer-history'
              >
                <FormattedMessage id='SETTINGS_PAGE.TRANSFERS' />
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                className={
                  `nav-link text-active-success me-6 ` +
                  (tabUrl === 'submit-request' && 'active')
                }
                to='/account?tab=submit-request'
              >
                <FormattedMessage id='SETTINGS_PAGE.SUPPORT' />
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  )
}

export {AccountHeader}
