import axios from "axios";

export const GET_TRANSFER_HISTORY_URL = `${process.env.REACT_APP_API_URL}/get-transfer-history.php`;

export function getTransferHistory(userID: string) {
    return axios({
      method: 'post',
      url: GET_TRANSFER_HISTORY_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      },
      data: {
        userID
      } 
    });
}