/* eslint-disable jsx-a11y/anchor-is-valid */
import { CircularProgress } from '@mui/material'
import { useState, useEffect } from 'react'
import { FormattedMessage } from 'react-intl'

type Props = {
    claimData: Array<any>
    loading: boolean
}

const tokenType: any = {
    '0' : 'Gold',
    '1': 'Platinum',
    '2': 'Diamond'
}

const EthereumRoyaltyClaimHistory: React.FC<Props> = ({claimData, loading}) => {

    const shrinkWalletAddress = (walletAddress: string) => {
        const walletStart = walletAddress.slice(0, 6)
        const walletEnd = walletAddress.slice(35)
    
        return walletStart + '......' + walletEnd
    }

    return (
        <div className={`card`}>
        {/* begin::Header */}
        <div className='card-header border-0 pt-5'>
            <h3 className='card-title align-items-start flex-column'>
                <span className='card-label fw-bolder fs-3 mb-1'><FormattedMessage id='SETTINGS_PAGE.ROYALTY_CLAIM_HISTORIES_ETH' /></span>
                {/* <span className='text-muted mt-1 fw-bold fs-7'>Over 500 new products</span> */}
            </h3>
        </div>
        {/* end::Header */}
        {/* begin::Body */}
        <div className='card-body py-3'>
            {/* begin::Table container */}
            <div className='table-responsive'>
            {/* begin::Table */}
            <table className='table align-middle gs-0 gy-4'>
                {/* begin::Table head */}
                <thead>
                    <tr className='fw-bolder text-muted bg-light'>
                        <th className='ps-4 min-w-150px rounded-start'><FormattedMessage id='SETTINGS_PAGE.TOKEN_DETAILS' /></th>
                        <th className='text-center min-w-125px'><FormattedMessage id='ADMIN_COLLECTION_PREVIEW.DEPOSIT_NO' /></th>
                        <th className='text-center min-w-125px'><FormattedMessage id='SETTINGS_PAGE.CLAIMER_ADDRESS' /></th>
                        <th className='text-center min-w-200px'><FormattedMessage id='SETTINGS_PAGE.CLAIMED_AMOUNT' /></th>
                        <th className='text-center min-w-200px text-end rounded-end pe-5'><FormattedMessage id='SETTINGS_PAGE.TRANSACTIONS' /></th>
                    </tr>
                </thead>
                {/* end::Table head */}
                {/* begin::Table body */}
                {!loading && 
                    <tbody>
                        {claimData.map((data: any) => (
                            <tr>
                                <td>
                                <div className='d-flex align-items-center'>
                                    <div className='symbol symbol-50px me-5'>
                                    <img
                                        src={`${process.env.REACT_APP_API_TOKEN_IMAGE_URL}/${data.token_image}`}
                                        className=''
                                        alt=''
                                    />
                                    </div>
                                    <div className='d-flex justify-content-start flex-column'>
                                    <span className='text-dark fw-bolder text-hover-primary mb-1 fs-6'>
                                        {data.collection_name}
                                    </span>
                                    <span className='text-muted fw-bold text-muted d-block fs-7'>
                                        <FormattedMessage id='CREATE_EDIT_COLLECTION.TIER' /> : 
                                        <span>{tokenType[data.tier.tier]}</span>
                                    </span>
                                    </div>
                                </div>
                                </td>
                                <td>
                                <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                    {data.depositNumber}
                                </span>
                                {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Paid</span> */}
                                </td>
                                <td>
                                <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                    {shrinkWalletAddress(data.claimer)}
                                </span>
                                {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Rejected</span> */}
                                </td>
                                <td>
                                <span className='text-center text-dark fw-bolder text-hover-primary d-block mb-1 fs-6'>
                                    {parseFloat(data.claimedAmount) / 10 ** 18} ETH
                                </span>
                                {/* <span className='text-muted fw-bold text-muted d-block fs-7'>Insurance</span> */}
                                </td>
                                <td className='text-center text-end pe-5'>
                                    <a
                                        href={`https://goerli.etherscan.io/tx/${data.txHash}`}
                                        className='btn btn-bg-light btn-active-color-primary me-1'
                                    >
                                        <FormattedMessage id='SETTINGS_PAGE.VIEW_ON_ETHERSCAN' />
                                    </a>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                }
                {/* end::Table body */}
            </table>
            {/* end::Table */}
            </div>
            {/* end::Table container */}
        </div>
        {/* begin::Body */}
        </div>
    )
}

export {EthereumRoyaltyClaimHistory}
