import { useState } from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { useNavigate, useLocation } from "react-router-dom"
import { RootState } from "setup";
import { WalletOptionDialogBox } from "./components/WalletOtionsDialog"
import {actions} from '../auth/redux/AuthRedux'
import { toAbsoluteUrl } from "_metronic/helpers";
import { updateWallet } from './api'
import { FormattedMessage } from 'react-intl'
import { AlertDanger } from "../alerts/Alerts";

declare var window: any

const ethChainIdMap: any = {
    '0x1': 'Ethereum Main Network (Mainnet)',
    '0x3': 'Ropsten Test Network',
    '0x4': 'Rinkeby Test Network',
    '0x5': 'Goerli Test Network',
    '0x2a': 'Kovan Test Network'
}

const klayNetworkMap: any = {
    '8217' : 'Cypress Mainnet',
    '1001' : 'Baobab Testnet'
}

const getCheckStatus = (state: any) => {
    if(state && state.message && state.message.length > 0) {
        return true
    }
    return false
}

const WalletLoginNew = () => {

    const { state }: any = useLocation()

    const [message, setMessage] = useState<string>(state && state.message ? state.message : '')
    const [checked, setChecked] = useState<boolean>(getCheckStatus(state))

    const [open, setOpen] = useState<boolean>(false);
    const [metamaskConnecting, setMetamaskConnecting] = useState<boolean>(false)
    const [kaikasConnecting, setKaikasConnecting] = useState<boolean>(false)
    const navigate = useNavigate()
    const {walletAddress, walletName, userID, chainID, custWalletEth, custWalletKlay,}: any = useSelector<RootState>(
        ({auth}) => ({
            walletAddress: auth.walletAddress,
            walletName: auth.walletName,
            userID: auth.user?.userID,
            chainID: auth.chainID,
            custWalletEth: auth.userWalletList.ethCustodialWallet,
            custWalletKlay: auth.userWalletList.klayCustodialWallet,
        }), shallowEqual
    )
    const dispatch = useDispatch()

    if(message !== "") {
        setTimeout(() => {
          setChecked(false);
          setTimeout(() => {
            setMessage("");
          }, 200);
        }, 5000);
    }

    function handleClickOpen() {
        setOpen(true);
    }

    function handleClose() {
        setOpen(false);
    }

    function signOutWallet() {
        dispatch(actions.signOutWallet())
    }

    async function setMetaMaskAccountInfo(accounts: any) {
        if (accounts.length === 0) {
            // MetaMask is locked or the user has not connected any accounts
            console.log('Please connect to MetaMask.');
        } else if (accounts[0] !== walletAddress) {
            let ethereum: any = window.ethereum
            const chainId = await ethereum.request({ method: 'eth_chainId' });
            updateWallet(userID, accounts[0], 'metamask')
            .then((response) => {
                if(response.data.success) {
                    setMetamaskConnecting(false)
                    // dispatch(actions.setWalletAddress(accounts[0], 'metamask', chainId))
                    const userWalletList = {
                        ethCustodialWallet: response.data.custWalletEth,
                        klayCustodialWallet: response.data.custWalletKlay,
                        metamaskWallet: response.data.metamask_wallet,
                        kaikasWallet: response.data.kaikas_wallet
                    }
                    dispatch(actions.setUserWalletList(userWalletList, accounts[0], 'metamask', chainId))
                } else {
                    if(response.data.message) {
                        setChecked(true)
                        setMessage(response.data.message)
                    } else {
                        setChecked(true)
                        setMessage('Cannot connect with a wallet, please try with another wallet')
                    }
                }
            })
        }
    }

    function handleMetaMaskLogin() {
        setMetamaskConnecting(true)
        if(window.ethereum != undefined) {
            let ethereum: any = window.ethereum
            ethereum
            .request({ method: 'eth_requestAccounts' })
            .then(setMetaMaskAccountInfo)
            .catch((err: any) => {
                if (err.code === 4001) {
                    console.log(err)
                } else {
                    console.error(err);
                }
            });
        } else {
            window.location.href = "https://metamask.io/download/"
        }
    }

    async function handleKaikasLogin() {
        setKaikasConnecting(true)
        const { klaytn } = window

        if (klaytn) {
            try {
                // if(klaytn.selectedAddress) {

                //     const account = klaytn.selectedAddress
                //     const networkVersion = klaytn.networkVersion
                //     console.log(account)
                    
                //     updateWallet(userID, account, 'kaikas')
                //     .then((response) => {
                //         if(response.data.success) {

                //             const userWalletList = {
                //                 ethCustodialWallet: response.data.custWalletEth,
                //                 klayCustodialWallet: response.data.custWalletKlay,
                //                 metamaskWallet: response.data.metamask_wallet,
                //                 kaikasWallet: response.data.kaikas_wallet
                //             }

                //             // dispatch(actions.setWalletAddress(account, 'kaikas'))
                //             dispatch(actions.setUserWalletList(userWalletList, account, 'kaikas', networkVersion))
                //         } else {
                //             if(response.data.message) {
                //                 setChecked(true)
                //                 setMessage(response.data.message)
                //             } else {
                //                 setChecked(true)
                //                 setMessage('Cannot connect with a wallet, please try with another wallet')
                //             }
                //         }
                //     })
                // } else {
                //     await klaytn.enable()
                // }
                await klaytn.enable()
                // setKaikasAccountInfo()
                const account = await klaytn.selectedAddress
                console.log(account)
        
                if(account) {
                    const networkVersion = await klaytn.networkVersion

                    updateWallet(userID, account, 'kaikas')
                    .then((response) => {
                        if(response.data.success) {

                            const userWalletList = {
                                ethCustodialWallet: response.data.custWalletEth,
                                klayCustodialWallet: response.data.custWalletKlay,
                                metamaskWallet: response.data.metamask_wallet,
                                kaikasWallet: response.data.kaikas_wallet
                            }

                            dispatch(actions.setUserWalletList(userWalletList, account, 'kaikas', networkVersion))
                            setKaikasConnecting(false)
                        } else {
                            if(response.data.message) {
                                setChecked(true)
                                setMessage(response.data.message)
                            } else {
                                setChecked(true)
                                setMessage('Cannot connect with a wallet, please try with another wallet')
                            }
                            setKaikasConnecting(false)
                        }
                    })
                } else {
                    handleKaikasLogin()
                }
            } catch (error) {
                console.log('User denied account access')
            }
        } else {
            window.location.href = "https://chrome.google.com/webstore/detail/kaikas/jblndlipeogpafnldhgmapagcccfchpi"
        }
    }

    const handleKaikasLogin2 = async () => {
        setKaikasConnecting(true)
        const { klaytn } = window

        if(klaytn) {
            try {
                await klaytn.enable()
                setKaikasAccountInfo()
            } catch (error) {
                console.log('User denied account access')
            }
        } else {
            window.location.href = "https://chrome.google.com/webstore/detail/kaikas/jblndlipeogpafnldhgmapagcccfchpi"
        }
    }

    const setKaikasAccountInfo = () => {
        const { klaytn } = window
        if (klaytn === undefined) return

        const account = klaytn.selectedAddress
        const networkVersion = klaytn.networkVersion

        updateWallet(userID, account, 'kaikas')
        .then((response) => {
            if(response.data.success) {

                const userWalletList = {
                    ethCustodialWallet: response.data.custWalletEth,
                    klayCustodialWallet: response.data.custWalletKlay,
                    metamaskWallet: response.data.metamask_wallet,
                    kaikasWallet: response.data.kaikas_wallet
                }

                dispatch(actions.setUserWalletList(userWalletList, account, 'kaikas', networkVersion))
                setKaikasConnecting(false)
            } else {
                if(response.data.message) {
                    setChecked(true)
                    setMessage(response.data.message)
                } else {
                    setChecked(true)
                    setMessage('Cannot connect with a wallet, please try with another wallet')
                }
                setKaikasConnecting(false)
            }
        })
    }

    return (
        <>
            {message && message != '' && 
                <AlertDanger message={message} checked={checked} />
            }

            <div className='card mb-5 mb-xl-10'>
                <>
                    <div className="d-flex w-100 justify-content-center pt-5">
                        <div className='mb-10 fv-row w-50'>
                            <div style={{ marginBottom: '50px' }}>
                                <h4><FormattedMessage id="SETTINGS_PAGE.WALLETS" /></h4>
                                <hr className="w-25 mb-10" />

                                <span className="mb-10">
                                    <FormattedMessage id="SETTINGS_PAGE.WALLETS.INFO" />
                                </span>
                            </div>

                            <div style={{ marginBottom: '50px' }}>
                                <h4><FormattedMessage id="SETTINGS_PAGE.CUSTODIAL_WALLETS" /></h4>
                                <hr className="w-25 mb-10" />

                                {custWalletEth && 
                                    <div className="d-flex">
                                        <div>
                                            <img 
                                                style={{ width: '35px', height: '35px' }}
                                                src={toAbsoluteUrl("/musician/logo.png")} 
                                                className="rounded"
                                            />
                                        </div>
                                        <div className="d-flex flex-column ms-6">
                                            <span>
                                                Ethereum
                                            </span>
                                            <span>
                                                {custWalletEth}
                                            </span>
                                        </div>
                                    </div>
                                }

                                {custWalletKlay && 
                                    <div className="d-flex mt-5">
                                        <div>
                                            <img 
                                                style={{ width: '35px', height: '35px' }}
                                                src={toAbsoluteUrl("/musician/logo.png")} 
                                                className="rounded"
                                            />
                                        </div>
                                        <div className="d-flex flex-column ms-6">
                                            <span>
                                                Klaytn
                                            </span>
                                            <span>
                                                {custWalletKlay}
                                            </span>
                                        </div>
                                    </div>
                                }

                                {!custWalletEth && !custWalletKlay && 
                                    <span><FormattedMessage id="SETTINGS_PAGE.NO_CUSTODIAL_WALLETS" /></span>
                                }
                            </div>

                            <div style={{ marginBottom: '50px' }}>
                                <h4>
                                    <FormattedMessage id="SETTINGS_PAGE.CONNECTED_WALLETS" />
                                </h4>
                                <hr className="w-25 mb-10" />

                                {walletAddress && 
                                    <div className="d-flex">
                                        <div>
                                            {walletName == 'metamask' ? (
                                                <img 
                                                    style={{ width: '100px', height: '100px' }}
                                                    src={toAbsoluteUrl("/media/wallet-icons/metamask.png")} 
                                                    className="rounded"
                                                />
                                            ) : (
                                                <img 
                                                    style={{ width: '50px', height: '50px' }}
                                                    src={toAbsoluteUrl("/media/wallet-icons/klaytn.png")} 
                                                    className="rounded"
                                                />
                                            )}
                                            
                                        </div>
                                        <div className="d-flex flex-column ms-3">
                                            <span>
                                                {walletName && walletName == 'metamask' ? 'Ethereum' : 'Klaytn' }
                                            </span>
                                            <span>
                                                {walletName == 'metamask' ? ethChainIdMap[chainID] : klayNetworkMap[chainID]}
                                            </span>
                                            <span>
                                                {walletAddress}
                                            </span>

                                            <button
                                                className="py-1 w-50 mt-2"
                                                style={{
                                                    background: '#ffd600',
                                                    border: '#0000',
                                                }}
                                                onClick={signOutWallet}
                                            >
                                                <FormattedMessage id="SETTINGS_PAGE.REMOVE_WALLET" />
                                            </button>
                                        </div>
                                    </div>
                                }

                                {!walletAddress && 
                                    <>
                                        <div className="d-flex mb-5">
                                            <div>
                                                <img 
                                                    style={{ width: '70px', height: '70px' }}
                                                    src={toAbsoluteUrl("/media/wallet-icons/metamask.png")} 
                                                    className="rounded"
                                                />
                                            </div>
                                            <div className="d-flex flex-column ms-3">
                                                <span className="mb-3">
                                                    <FormattedMessage id="SETTINGS_PAGE.ADD_ETH_WALLET1" />
                                                </span>
                                                <button
                                                    className="py-1"
                                                    style={{
                                                        background: '#ffd600',
                                                        border: '#0000',
                                                    }}
                                                    onClick={handleMetaMaskLogin}
                                                >
                                                    
                                                    <FormattedMessage id="SETTINGS_PAGE.ADD_ETH_WALLET2" />
                                                    {metamaskConnecting && 
                                                        <span className='spinner-border spinner-border-sm align-middle ms-3'></span>
                                                    }
                                                </button>
                                            </div>
                                        </div>

                                        <div className="d-flex ms-3">
                                            <div>
                                                <img 
                                                    style={{ width: '50px', height: '50px' }}
                                                    src={toAbsoluteUrl("/media/wallet-icons/klaytn.png")} 
                                                    className="rounded"
                                                />
                                            </div>
                                            <div className="d-flex flex-column ms-6">
                                                <span className="mb-3"><FormattedMessage id="SETTINGS_PAGE.ADD_KLAY_WALLET1" /></span>
                                                <button 
                                                    className="py-1"
                                                    style={{
                                                        background: '#ffd600',
                                                        border: '#0000',
                                                    }}
                                                    onClick={handleKaikasLogin2}
                                                >
                                                    <FormattedMessage id="SETTINGS_PAGE.ADD_KLAY_WALLET2" />
                                                    {kaikasConnecting && 
                                                        <span className='spinner-border spinner-border-sm align-middle ms-3'></span>
                                                    }
                                                </button>
                                            </div>
                                        </div>
                                    </>
                                }
                            </div>
                        </div>
                    </div>
                </>
            </div>
        </>
    )
}

export default WalletLoginNew