import axios from 'axios';

const UPDATE_WALLET_URL = `${process.env.REACT_APP_API_URL}/update-connected-wallet-for-user.php`;


export function updateWallet(userID: string, walletAddress: string, walletName: string) {
    return axios({
      method: 'post',
      url: UPDATE_WALLET_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      },
      data: {
        userID,
        walletAddress,
        walletName
      }
    })
}