import React, {useState} from 'react'
import {useSelector, shallowEqual, useDispatch} from 'react-redux'
import {toAbsoluteUrl} from '../../../../../_metronic/helpers'
import * as Yup from 'yup'
import {useFormik} from 'formik'
import {RootState} from '../../../../../setup'
import { submitRequest } from './_redux/accountCrud'
import * as auth from '../../../auth/redux/AuthRedux'
import {UserModel} from '../../../auth/models/UserModel'
import {AlertSuccess, AlertDanger} from '../../../alerts/Alerts'
import { FormattedMessage, useIntl } from 'react-intl'


interface SubmitRequestModel {
    email: string
    subject: string
    description: string
}

const SubmitRequest: React.FC = () => {
  const [loading, setLoading] = useState(false)
  const [checked, setChecked] = useState(false);
  const [resSuccessMessage, setResSuccessMessage] = useState("");
  const [resFailureMessage, setResFailureMessage] = useState("");

  const intl = useIntl()

  const submitRequestFormSchema = Yup.object().shape({
    email: Yup.string()
      .email(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.WRONG_FORMAT'}))
      .min(3, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MIN'}))
      .max(50, intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.MAX'}))
      .required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.EMAIL.REQUIRED'})),
    subject: Yup.string().required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.HANDLE.REQUIRED'})),
    description: Yup.string().required(intl.formatMessage({id: 'PROFILE_DETAIL_SCHEMA.HANDLE.REQUIRED'})),
  })

  if(resSuccessMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setResSuccessMessage("");
      }, 300);
    }, 5000);
  }

  if(resFailureMessage !== "") {
    setTimeout(() => {
      setChecked(false);
      setTimeout(() => {
        setResFailureMessage("");
      }, 300);
    }, 5000);
  }

  const dispatch = useDispatch()
  const {fullname, email, username, authToken, userID, description} : any = useSelector<RootState>(
    ({auth}) => ({
      authToken: auth.accessToken,
      userID: auth.user?.userID,
      fullname: auth.user?.fullname,
      email: auth.user?.email,
      username: auth.user?.username,
      description: auth.user?.description
  }), shallowEqual)

  const initialValues: SubmitRequestModel = {
    email: email,
    subject: "",
    description: ""
  }

  const formik = useFormik<SubmitRequestModel>({
    initialValues,
    validationSchema: submitRequestFormSchema,
    onSubmit: (values) => {
      setLoading(true)
      setTimeout(() => {
        submitRequest(
            // authToken,
            values.email,
            values.subject,
            values.description
        )
          .then((response) => {
            if(response.data.success) {
                setChecked(true)
                setResSuccessMessage(response.data.message)
                setLoading(false)
            } else {
                if(response.data.message) {
                    setResFailureMessage(response.data.message);
                    setLoading(false);
                    setChecked(true);
                } else {
                    setResFailureMessage(intl.formatMessage({ id: "SUPPORT.REQUEST_SUB.FAILED" }));
                    setLoading(false);
                    setChecked(true);
                }
            }
          })
          .catch((error) => {
            localStorage.setItem('sessionTimeOut', error);
            auth.actions.logout()
          })
      }, 1000)
    },
  })

  return (
    <>
    {resSuccessMessage !== ""  ? (
        <AlertSuccess message={resSuccessMessage} checked={checked} />
      ) : null}

    {resFailureMessage !== ""  ? (
        <AlertDanger message={resFailureMessage} checked={checked} />
      ) : null}
      
    <div className='card mb-5 mb-xl-10'>
      <div
        className='card-header border-0 cursor-pointer'
        role='button'
        data-bs-toggle='collapse'
        data-bs-target='#kt_account_profile_details'
        aria-expanded='true'
        aria-controls='kt_account_profile_details'
      >
        <div className='card-title m-0'>
          <h3 className='fw-bolder m-0'><FormattedMessage id='SETTINGS_PAGE.SUBMIT_REQUEST_TITLE' /></h3>
        </div>
      </div>

      <div id='kt_account_profile_details' className='show'>
        <form onSubmit={formik.handleSubmit} noValidate className='form'>
          <div className='card-body border-top p-9'>

            <div className='row mb-6'>
              <label className='col-lg-4 col-form-label required fw-bold fs-6 text-muted'>
                <FormattedMessage id='SETTINGS_PAGE.YOUR_EMAIL' />
              </label>
              <div className='col-lg-8 fv-row'>
                <input
                  type='text'
                  className='form-control form-control-lg form-control-solid text-muted'
                  placeholder='Email'
                  {...formik.getFieldProps('email')}
                  disabled
                />
                {formik.touched.email && formik.errors.email && (
                  <div className='fv-plugins-message-container'>
                    <div className='fv-help-block'>{formik.errors.email}</div>
                  </div>
                )}
              </div>
            </div>
 
            <div className='row mb-6'>
              <label className='col-lg-4 col-form-label required fw-bold fs-6'>
                <FormattedMessage id='SETTINGS_PAGE.SUBJECT' />
              </label>
              <div className='col-lg-8 fv-row'>
                <input
                  type='text'
                  className='form-control form-control-lg form-control-solid'
                  {...formik.getFieldProps('subject')}
                />
                {formik.touched.subject && formik.errors.subject && (
                  <div className='fv-plugins-message-container'>
                    <div className='fv-help-block'>{formik.errors.subject}</div>
                  </div>
                )}
              </div>
            </div>

            <div className='row mb-6'>
              <label className='col-lg-4 col-form-label fw-bold fs-6'>
                <FormattedMessage id='SETTINGS_PAGE.DESCRIPTION' />
              </label>
              <div className='col-lg-8 fv-row'>
                <textarea
                  className='form-control form-control-lg form-control-solid'
                  rows={5}
                  {...formik.getFieldProps('description')}
                />
                {formik.touched.description && formik.errors.description && (
                  <div className='fv-plugins-message-container'>
                    <div className='fv-help-block'>{formik.errors.description}</div>
                  </div>
                )}
              </div>
            </div>

          </div>

          <div className='card-footer d-flex justify-content-end py-6 px-9'>
            <button type='submit' style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} className='py-2 px-4 rounded' disabled={loading}>
              {!loading && <FormattedMessage id='SETTINGS_PAGE.SUBMIT_REQUEST_BTN' />}
              {loading && (
                <span className='indicator-progress' style={{display: 'block'}}>
                  <FormattedMessage id='SETTINGS_PAGE.PLEASE_WAIT' />...{' '}
                  <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
    </>
  )
}

export {SubmitRequest}