import { useState, useEffect } from "react"
import { useSelector, shallowEqual } from "react-redux"
import { RootState } from "setup"
import { EthereumRoyaltyClaimHistory } from "./components/EthereumRoyaltyClaimHistory"
import { KlaytnRoyaltyClaimHistory } from "./components/KlaytnRoyaltyClaimHistory"
import { getKlaytnRoyaltyClaimTransactionHistory } from "../_redux/accountCrud"
import { getETHRoyaltyClaimTransactionHistoryData } from "app/modules/dropDetail/dropDetailEthereum/queries/Queries"

const RoyaltyClaimHistory = (props: any) => {

    const [claimData, setClaimData] = useState([])
    const [loading, setLoading] = useState<boolean>(true)

    const {walletAddress, walletName}: any = useSelector<RootState>(
        ({auth}) => ({
            walletAddress: auth.walletAddress,
            walletName: auth.walletName
        })
    )

    useEffect(() => {
        if(walletName == 'kaikas') {
            getKlaytnRoyaltyClaimTransactionHistory(walletAddress)
            .then((response) => {
                if(response.data.success) {
                    setClaimData(response.data.claimData)
                    setLoading(false)
                }
            })
        } else if(walletName == 'metamask') {
            getETHRoyaltyClaimTransactionHistoryData(walletAddress)
            .then((data: any) => {
                setClaimData(data)
                setLoading(false)
            })
        }
    }, [walletName])

    return (
        <>
            {walletName == 'metamask' && 
                <EthereumRoyaltyClaimHistory 
                    claimData={claimData}
                    loading={loading}
                />
            }  
            {walletName == 'kaikas' && 
                <KlaytnRoyaltyClaimHistory 
                    claimData={claimData}
                    loading={loading}
                />
            }  
        </>
    )
}

export {RoyaltyClaimHistory}