import React, {useState, useEffect} from 'react'
import {PageLink, PageTitle} from '../../../_metronic/layout/core'
import {Settings} from './components/settings/Settings'
import {AccountHeader} from './AccountHeader'
import {useSearchParams} from 'react-router-dom'
import WalletLogin from '../wallets/WalletLogin'
import WalletLoginNew from '../wallets/WalletLoginNew'
import { TransactionHistory } from './components/settings/transacionHistory/TransactionHistory'
import { RoyaltyClaimHistory } from './components/settings/royaltyClaimHistory/RoyaltyClaimHistory'
import { SubmitRequest } from './components/settings/Support'
import { CardHistory } from './components/settings/cardHistory/CardHistory'
import { LanguageChange } from './components/settings/cards/LanguageChange'
import { TransferHistory } from './components/settings/transferHistory/TransferHistory'

const accountBreadCrumbs: Array<PageLink> = [
  {
    title: 'Account',
    path: '/crafted/account/overview',
    isSeparator: false,
    isActive: false,
  },
  {
    title: '',
    path: '',
    isSeparator: true,
    isActive: false,
  },
]



const AccountPage: React.FC = () => {
  const [params] = useSearchParams()
  const url: string | null = params.get("tab")
  
  const [tabUrl, setTabUrl] = useState(url)

  useEffect(() => {
    setTabUrl(url)
  }, [url])

  return (
    <>
      <AccountHeader />

      {tabUrl === 'settings' &&
        <Settings />
      }

      {/* {tabUrl === 'language' &&
        <LanguageChange />
      } */}

      {tabUrl === 'your-wallet' &&
        // <WalletLogin />
        <WalletLoginNew />
      }

      {/* {tabUrl === 'transaction-history' &&
        <TransactionHistory />
      } */}

      {tabUrl === 'card-history' &&
        <CardHistory />
      }

      {tabUrl === 'royalty-claim-transaction-history' &&
        <RoyaltyClaimHistory />
      }

      {tabUrl === 'transfer-history' &&
        <TransferHistory />
      }

      {tabUrl === 'submit-request' &&
        <SubmitRequest />
      }
    </>
  )
}

export default AccountPage
